<template>
  <div class="wrapper">

    <!-- header部分 -->
    <header>
      <p>头像管理</p>
    </header>

    <div class="userinfo">
      <img v-bind:src="imgSrc" class="userinfo-img" id="userDefaultImgBox">
             <ul id="userAlternateImgArrBox">
  					<li><img src="../assets/dcfl01.png"></li>
  					<li><img src="../assets/dcfl02.png"></li>
  					<li><img src="../assets/dcfl03.png"></li>
  					<li><img src="../assets/dcfl04.png"></li>
  					<li><img src="../assets/dcfl05.png"></li>
  					<li><img src="../assets/dcfl06.png"></li>
  					<li><img src="../assets/dcfl07.png"></li>
  					<li><img src="../assets/dcfl08.png"></li>
  					
  				</ul> -->
      <div>
        <input type="file" ref="myfile" v-on:change="changeImg()">
      </div>
      <button v-on:click="saveImg()">保存修改</button>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
import Footer from '../components/Footer.vue'
export default {
  name: 'MyImg',
  data: function () {
    return {
      imgSrc: ''
    }
  },
  methods: {
    changeImg: function () {
      let myfile = this.$refs.myfile;
      // console.log(myfile);
      let myimg = myfile.files[0];
      // console.log(myimg);
      // 原生JS读取文件的内置对象
      let reader = new FileReader();
      // 把文件路径提供给文件读取工具
      reader.readAsDataURL(myimg);
      reader.onload = e => {
        let data = e.target.result;
        // console.log(data);
        this.imgSrc = data;
      }
    },
    // 保存头像
    saveImg: function () {
      let url = 'UserController/updateUserImg';
      let that = this;
      this.$axios.post(url,this.$qs.stringify({
          userId: that.$getSessionStorage().userId,
          userImg: that.imgSrc
      }))
        .then(resp=>{
          let result = resp.data;
          if(result > 0){
            alert("头像更改成功");
          }else{
            alert('更改失败');
          }
        })
        .catch(err=>{
          console.log(err);
        })

    },
    // 获取用户头像信息
    getUserById: function (userId) {
      let that = this;
      let url = 'UserController/getUserById';
      this.$axios.post(url, this.$qs.stringify({ userId }))
        .then(resp => {
          let user = resp.data;
          that.imgSrc = user.userImg;
        })
        .catch(err => {
          console.log(err);
        })
    }
  },
  components: {
    Footer: Footer
  },
  created: function () {
    let user = this.$getSessionStorage();
    if (user != null) {
      this.getUserById(user.userId);
    }
  }
}
</script>

