<template>
	<div class="wrapper">
		<!-- header部分 -->
		<header>
			<p>用户注册</p>
		</header>
		<!-- 表单部分 -->
		<ul class="form-box">
			<li>
				<div class="title">
					手机号码：
				</div>
				<div class="content">
					<input type="text" @blur="checkUserId" v-model="user.userId" placeholder="手
机号码" pattern="^1[358][0-9]{9}$"
					 maxlength="11">
				</div>
			</li>
			<li>
				<div class="title">
					密码：
				</div>
				<div class="content">
					<input type="password" v-model="user.password" placeholder="至少为6位,需包含大、小写字母和数字" pattern="[A-Za-z0-9]{6,}" title="密码需包含且只能包含大、小写字母和数字，长度至少为6位">
				</div>
			</li>
			<li>
				<div class="title">
					确认密码：
				</div>
				<div class="content">
					<input type="password" v-model="confirmPassword" placeholder="确认密码" pattern="[A-Za-z0-9]{6,}">
				</div>
			</li>
			<li>
				<div class="title">
					用户名称：
				</div>
				<div class="content">
					<input type="text" v-model="user.userName" placeholder="长度2-16字符,只能包含汉字，大小写字母或下划线" pattern="[\u4e00-\u9fa5A-Za-z_]{2,16}"
					 title="用户名称只能包含汉字，大小写字母和下划线,长度2-16个字符">
				</div>
			</li>
			<li>
				<div class="title">
					性别：
				</div>
				<div class="content" style="font-size: 3vw;">
					<input type="radio" v-model="user.userSex" value="1" style="width:6vw;height: 3.2vw;">男
					<input type="radio" v-model="user.userSex" value="0" style="width:6vw;height: 3.2vw;">女
				</div>
			</li>
			<li>
			      <div class="title">
			        验证码：
			      </div>
			      <div class="content">
			        <verify-code 
			          ref="verifyCode"
			          :length="4"
			          :width="160"
			          :height="60"
			          :resetable="true"
			          @change="handleVerifyCodeChange"
			        ></verify-code>
			        <button @click="resetVerifyCode">重新生成</button>
			      </div>
			    </li>


		</ul>
		<div class="button-login">
			<button @click="register">注册</button>
		</div>
		<!-- 底部菜单部分 -->
		<Footer></Footer>
	</div>
</template>
<script>
	import VerifyCode from 'vue-verify-code';
	import Footer from '../components/Footer.vue';
	// 生成随机验证码
	function generateRandomCode(length) {
		let code = '';
		const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
		for (let i = 0; i < length; i++) {
			code += characters.charAt(Math.floor(Math.random() * characters.length));
		}
		return code;
	}
	export default {
		name: 'Register',
		data() {
			return {
				user: {
					userId: '',
					password: '',
					userName: '',
					userSex: 1,

				},
				confirmPassword: '',
				captcha: '',
				confirmPassword: '',
				    generatedCode: '',
				    userInputCode: ''
			}
		},
		methods: {
			// generateCaptcha() {
			// 	this.captcha = generateRandomCode(6);
			// },
			
			  verifyCode() {
			    if (this.generatedCode.code === this.userInputCode) {
			        alert('验证码验证通过！');
			        // 验证通过后的逻辑
			      } else {
			        alert('验证码不正确！');
			        // 验证不通过的逻辑
			      }
			      // 重置验证码
			      this.$refs.verifyCode.reset();
			      this.userInputCode = '';
			   
			  },
			  handleVerifyCodeChange(generatedCode) {
			    this.generatedCode = generatedCode;
			  },
			resetVerifyCode() {
				this.$refs.verifyCode.reset();
			},
			checkUserId() {
				this.$axios.post('UserController/getUserById', this.$qs.stringify({
					userId: this.user.userId,
				})).then(response => {
					if (response.data == 1) {
						this.user.userId = '';
						alert('此手机号码已存在！')
					}
				}).catch(error => {
					console.error(error);
				});
			},
			register() {

				if (!/^1[358][0-9]{9}$/.test(this.user.userId)) {
					alert('请输入正确的手机号!');
					return;
				}

				if (!/^[A-Za-z0-9]{6,}$/.test(this.user.password)) {
					alert('密码需包含且只能包含大、小写字母和数字，长度至少为6位！');
					return;
				}
				if (this.user.userId == '') {
					alert('手机号码不能为空！');
					return;
				}
				if (this.user.password == '') {
					alert('密码不能为空！');
					return;
				}
				if (this.user.password != this.confirmPassword) {
					alert('两次输入的密码不一致！');
					return;
				}
				if (this.user.userName == '') {
					alert('用户名不能为空！');
					return;
				}
				if (!/^[\u4e00-\u9fa5A-Za-z_]{2,16}$/.test(this.user.userName)) {
					alert('用户名称只能包含汉字，大小写字母和下划线,长度2-16个字符 ');
					return;
				}
				// 验证验证码
				/* if (this.captcha !== this.generatedCaptcha) {
					alert('验证码不正确！');
					return;
				} */
				//注册请求
				this.$axios.post('UserController/saveUser', this.$qs.stringify(
					this.user
				)).then(response => {
					if (response.data > 0) {
						alert('注册成功！');
						this.$router.go(-1);
					} else {
						alert('注册失败！');
					}

				}).catch(error => {
					console.error(error);
				});
			}
		},
		components: {
			Footer
		}
	}
</script>
<style scoped>
	/****************** 总容器 ******************/
	.wrapper {
		width: 100%;
		height: 100%;
	}

	/****************** header部分 ******************/
	.wrapper header {
		width: 100%;
		height: 12vw;
		background-color: #0097FF;
		color: #fff;
		font-size: 4.8vw;
		position: fixed;
		left: 0;
		top: 0;
		z-index: 1000;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	/****************** 表单部分 ******************/
	.wrapper .form-box {
		width: 100%;
		margin-top: 12vw;
	}

	.wrapper .form-box li {
		box-sizing: border-box;
		padding: 4vw 3vw 0 3vw;
		display: flex;
		align-items: center;
	}

	.wrapper .form-box li .title {
		flex: 0 0 18vw;
		font-size: 3vw;
		font-weight: 700;
		color: #666;
	}

	.wrapper .form-box li .content {
		flex: 1;
	}

	.wrapper .form-box li .content input {
		border: none;
		outline: none;
		width: 100%;
		height: 4vw;
		font-size: 3vw;
	}

	.wrapper .button-login {
		width: 100%;
		box-sizing: border-box;
		padding: 4vw 3vw 0 3vw;
	}

	.wrapper .button-login button {
		width: 100%;
		height: 10vw;
		font-size: 3.8vw;
		font-weight: 700;
		color: #fff;
		background-color: #38CA73;
		border-radius: 4px;
		border: none;
		outline: none;
	}

	.wrapper .button-register {
		width: 100%;
		box-sizing: border-box;
		padding: 4vw 3vw 0 3vw;
	}

	.wrapper .button-register button {
		width: 100%;
		height: 10vw;
		font-size: 3.8vw;
		font-weight: 700;
		color: #666;
		background-color: #EEE;
		border-radius: 4px;
		border: none;
		outline: none;
		border: solid 1px #DDD;

	}
</style>
